
body {
  background: #333;
}
#app {
  color: #ccc;
  margin-top: 30px;
}
#app > .body {
    font-size: 1.6em;
    padding-top: 30px;
}
nav .navbar-brand > i {
  color: #ddd;
  font-size: .8em;
}
nav code {
  background: #444;
  color: #eee;
}
nav form input[type=text] {
  max-width: 10em;
  background-color: #1a242f;
  color: #fff;
  border-color: transparent;
}
nav form input[type=text]:hover {
    border-color: #fff;
}



